/* ===== Forms ===== */

.form-control {
  border-radius: 0; /* */
}

/* ===== Navs ===== */

/* 1. Nav tabs */

.nav-tabs {
  margin-bottom: 15px;
}
.nav-tabs > li > a {
    border-bottom: 1px solid #dedede;
    border-radius: 0;
    background: #f5f5f5;
}

/* 2. Nav pills */

.nav-pills {
  margin-bottom: 15px;
}
.nav-pills > li > a {
  border-radius: 0;
}
.nav-pills > li > a {
  background-color: #f5f5f5;
}
.nav-pills-green > li.active > a,
.nav-pills-green > li.active > a:hover,
.nav-pills-green > li.active > a:focus {
  background-color: #72a53b; 
}
.nav-pills-blue > li.active > a,
.nav-pills-blue > li.active > a:hover,
.nav-pills-blue > li.active > a:focus {
  background-color: #00B4FF; 
}
.nav-pills-dark > li.active > a,
.nav-pills-dark > li.active > a:hover,
.nav-pills-dark > li.active > a:focus {
  background-color: #515151; 
}
.nav-pills-red > li.active > a,
.nav-pills-red > li.active > a:hover,
.nav-pills-red > li.active > a:focus {
  background-color: #E74C3C; 
}
.nav-pills-turquoise > li.active > a,
.nav-pills-turquoise > li.active > a:hover,
.nav-pills-turquoise > li.active > a:focus {
  background-color: #1ABC9C; 
}
.nav-pills-amethyst > li.active > a,
.nav-pills-amethyst > li.active > a:hover,
.nav-pills-amethyst > li.active > a:focus {
  background-color: #9B59B6; 
}

/* ===== Alerts ===== */

.alert {
  border-radius: 0;
}

/* ===== Thumbnails ===== */

.thumbnail {
  padding: 0;
  border: 0;
  border-radius: 0;
}
.thumbnail > img {
  display: block;
  max-width: 100%;
  height: auto;
  padding: 10px;
  border: 1px solid #eee;
}
a.thumbnail:hover img,
a.thumbnail:focus img {
  border-color: #ccc;
}
.thumbnail > img {
  margin-left: auto;
  margin-right: auto;
}
.thumbnail .caption {
  padding: 10px;
  color: #333333;
}
.thumbnail .caption h4{
  text-transform: uppercase;
}
.thumbnail .caption .rating {
  margin-bottom: 10px;
}
.thumbnail .caption .rating i {
  color: #F7C41F;
  font-size: 18px;
}

/* ===== Page Tip ===== */

.page-tip {
  background: #fff;
  -webkit-box-shadow: 0 0 16px 0 rgba(50, 50, 50, 0.3);
  -moz-box-shadow: 0 0 16px 0 rgba(50, 50, 50, 0.3);
  box-shadow: 0 0 16px 0 rgba(50, 50, 50, 0.3);
  color: #515151;
  margin-bottom: 50px;
  padding: 15px 0;
}
.page-tip p {
  margin: 0;
}

/* ===== Section headers ===== */

.section-header {
  background: #f5f5f5;
  border-bottom: 1px solid #dedede;
  padding: 15px 0;
  margin-bottom: 50px;
}
.section-header h1 {
  color: #666;
  font-size: 16px;
  font-weight: 300;
  text-transform: uppercase;
  padding: 0;
  margin: 0;
}
.section-header h1 span {
  border-bottom: 5px solid #00B4FF;
  padding-bottom: 10px;
}

/* ===== Block header ===== */

.block-header h2 {
  margin: 40px 0;
  font-size: 24px;
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  color: #515151;
  text-transform: uppercase;
  background: #eee;
}
.block-header h2 .title {
  background: #fff;
  padding: 0 15px 0 0;
}
.block-header h2 .decoration {
  background: #fff;
  padding-left: 3px;
  margin-left: 5px;
}

/* ===== Lead ===== */

.lead {
  font-weight: 300;
}

/* ===== Social links ===== */

.social-icons {
  margin-bottom: 15px;
}
.social-icons ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.social-icons ul li {
  text-align: center;
  float: left;
  margin: 0 5px 5px 0;
  opacity: 0.5;
  transition: 0.1s;
  -webkit-transition: 0.1s;
  -moz-transition: 0.1s;
}
.social-icons-small ul li {
  height: 20px;
  width: 20px;
  font-size: 16px;
  line-height: 20px;
}
.social-icons-default ul li {
  height: 30px;
  width: 30px;
  font-size: 22px;
  line-height: 30px;
}
.social-icons-large ul li {
  height: 40px;
  width: 40px;
  font-size: 28px;
  line-height: 40px;
}
.social-icons ul li:hover {
  opacity: 1;
  transition: 0.1s;
  -webkit-transition: 0.1s;
  -moz-transition: 0.1s;
}
.social-icons ul li a {
  color: #FFF;
  display: block;
}
.social-icons ul li.rss {
  background: #ff9900;
}
.social-icons ul li.facebook {
  background: #3B5998;
}
.social-icons ul li.twitter {
  background: #1dcaff;
}
.social-icons ul li.plus {
  background: #dd4b39;
}
.social-icons ul li.vk {
  background: #3f628a;
}
.social-icons ul li.pinterest {
  background: #c92026;
}
.social-icons ul li.vimeo {
  background: #446d82;
}
.social-icons ul li.github {
  background: #657e8c;
}
.social-icons ul li.linkedin {
  background: #0c7bb3;
}
.social-icons ul li.dropbox {
  background: #4fa3e3;
}

/* ===== Tags ===== */

.tags > a {
  padding: 3px 4px;
  margin: 4px 4px 4px 0;
  background: #f5f5f5;
  border: 1px solid #dedede;
  border-width: 0 1px 1px 0;
  color: #515151;
  text-decoration: none;
  font-size: 0.9em;
  white-space: nowrap;
  display: inline-block;
}
.tags > a:hover {
  background: #00B4FF;
  color: #FFF;
  border-color: #00B4FF;
}
.tags > a:before {
  font-family: FontAwesome;
  content: "\f02b";
  color: #999;
  margin-right: 5px;
}
.tags > a:hover:before {
  color: #FFF;
}